/* tslint:disable:export-name no-any */
export function serial(list: (() => Promise<any>)[]): Promise<any> {
/* tslint:enable:export-name */
  return list.reduce(
    (previousPromise: Promise<any>, nextPromiseFunction: () => Promise<any>) =>
/* tslint:enable:no-any */
      previousPromise.then(nextPromiseFunction),
    Promise.resolve());
}
